import React from 'react';
import { Box } from '@cko/primitives';
import { IconActionInfo } from '@cko/icons';
import TooltipButtonWrapper from 'components/TooltipButtonWrapper';

interface Props {
  text: string;
  enabled?: boolean;
}

const InfoTooltip: React.FunctionComponent<Props> = ({
  text,
  enabled = true,
}) => {
  return (
    <Box ml={1} display="inline-block">
      <TooltipButtonWrapper enabled={enabled} text={text}>
        <IconActionInfo style={{ verticalAlign: 'bottom' }} />
      </TooltipButtonWrapper>
    </Box>
  );
};

export default InfoTooltip;
