import React, { useEffect } from 'react';
import { Box } from '@cko/primitives';
import * as am4core from '@amcharts/amcharts4/core';
import * as am4charts from '@amcharts/amcharts4/charts';
import am4themesAnimated from '@amcharts/amcharts4/themes/animated';
import { Link } from 'types/models';
import { SankeyDisplayValue } from 'containers/DashboardContainer/SankeyContainer/SankeyContainer';

am4core.useTheme(am4themesAnimated);

interface Props {
  data: Link[];
  sankeyDisplayValue: SankeyDisplayValue;
}

const Sankey: React.FunctionComponent<Props> = ({
  data,
  sankeyDisplayValue,
}) => {
  useEffect(() => {
    let chart = am4core.create('chartdiv', am4charts.SankeyDiagram);
    chart.data = data;

    // for hover on links
    let hoverState = chart.links.template.states.create('hover');
    hoverState.properties.fillOpacity = 0.6;

    chart.dataFields.fromName = 'display_source';
    chart.dataFields.toName = 'display_target';
    chart.dataFields.value = 'value';
    chart.nodes.template.nameLabel.label.truncate = false;

    // to add the % to text on tooltip when hovering on links in the sankey
    chart.links.template.adapter.add('tooltipText', (text, target) => {
      if (sankeyDisplayValue === SankeyDisplayValue.percentage) {
        const dataContext = target.dataItem.dataContext as Link;
        return `{fromName} â†’ {toName} ${dataContext.percentage}%`;
      }

      return text;
    });

    // To prevent the smaller links to become invisible
    chart.links.template.adapter.add('linkWidth', (width, target) => {
      const widthTreshold = 1;
      if (width && width <= widthTreshold) {
        target.strokeWidth = 3;
        target.strokeOpacity = 0.3;
      }
      return width;
    });

    chart.colors.list = [
      am4core.color('#1be396'),
      am4core.color('#83e9b4'),
      am4core.color('#beeed2'),
      am4core.color('#c0c4ec'),
      am4core.color('#8b99e5'),
      am4core.color('#4371de'),
    ];

    chart.paddingTop = 16;
    chart.paddingBottom = 16;
    chart.paddingRight = 80;
    chart.paddingLeft = 16;

    // default styling for sankey
    chart.links.template.colorMode = 'gradient';

    return () => chart.dispose();
  }, [data, sankeyDisplayValue]);

  return (
    <Box p="16px" boxShadow={6}>
      <div
        data-testid="sankey-graph"
        id="chartdiv"
        style={{ width: '100%', height: '400px' }}
      />
    </Box>
  );
};

export default Sankey;
