import { Link } from 'types/models';
import { groupBy } from './groupBy';

const compareDecimals = (a: Link, b: Link) => {
  return (
    Math.floor(a.percentage) -
    a.percentage -
    (Math.floor(b.percentage) - b.percentage)
  );
};

export const calculateLinksRoundingPercentage = (links: Link[]) => {
  const linksByType = groupBy(links, (link) => {
    return link.type;
  });

  let linksWithRoundedPercentage: Link[] = [];

  Object.keys(linksByType).forEach((key) => {
    const links = linksByType[key];
    const diff =
      100 -
      links
        .map((link) => Math.floor(link.percentage))
        .reduce((acc, current) => acc + current, 0);

    const sortedByDecimalLinks = links.sort(compareDecimals);

    const percentageLinks = sortedByDecimalLinks.map((link, index) => {
      const percentage =
        index < diff
          ? Math.floor(link.percentage) + 1
          : Math.floor(link.percentage);
      return {
        ...link,
        percentage,
      } as Link;
    });

    return (linksWithRoundedPercentage = [
      ...linksWithRoundedPercentage,
      ...percentageLinks,
    ]);
  });

  return reorderArray<Link>(linksWithRoundedPercentage, links, (item) => {
    return item.id;
  });
};

const reorderArray = <T extends any>(
  unorderedArray: T[],
  orderedArray: T[],
  getItemProperty: (item: T) => any
) => {
  const array: T[] = [];
  unorderedArray.forEach((item) => {
    let itemIndex = orderedArray.findIndex(
      (orderedItem) => getItemProperty(orderedItem) === getItemProperty(item)
    );
    if (itemIndex !== -1) {
      array[itemIndex] = item;
    }
  });
  return array;
};
