import React, { useCallback, useState } from 'react';
import { IconActionDownload } from '@cko/icons';
import { formatDate } from 'utils/dates/dateFormatter';
import styled from 'styled-components';
import {
  PrimaryButton,
  Flex,
  InlineNotification,
  Box,
  OutlineButton,
} from '@cko/primitives';
import useDownloadFile from 'hooks/useDownloadFile';
import endpoints from 'api/endpoints';
import { MutateFunction } from 'react-query';
import { AxiosError } from 'axios';

interface Props {
  from: string;
  to: string;
  reportId: string;
  onClear: MutateFunction<
    {},
    AxiosError<{}>,
    {
      data: {};
      path?: string | undefined;
    },
    unknown
  >;
}

const formatDownloadDate = (date: string) => {
  return formatDate(new Date(date), 'dd MMM yyyy');
};

const DownloadIcon = styled(IconActionDownload)`
  margin-right: ${({ theme }) => theme.space[2]};

  & > g {
    fill: currentColor;
  }
`;

const DownloadReport: React.FunctionComponent<Props> = ({
  from,
  to,
  reportId,
  onClear,
}) => {
  const [hasError, setHasError] = useState(false);

  const { download, loading } = useDownloadFile(
    `${endpoints.assessmentReports}/${reportId}/download`,
    `prism-report-${formatDate(new Date(from))}-${formatDate(new Date(to))}.csv`
  );

  const handleDownload = useCallback(async () => {
    try {
      setHasError(false);
      await download();
    } catch {
      setHasError(true);
    }
  }, [download]);

  const handleReset = () => {
    onClear({ path: `${reportId}/reset`, data: {} });
  };

  return (
    <Flex alignItems="center">
      {hasError && (
        <Box mr={2}>
          <InlineNotification type="alert">
            Sorry, there was an error downloading your report. Please try again.
          </InlineNotification>
        </Box>
      )}
      <PrimaryButton onClick={handleDownload} disabled={loading}>
        <DownloadIcon />
        {!loading
          ? `Download report: ${formatDownloadDate(
              from
            )} - ${formatDownloadDate(to)}`
          : 'Downloading...'}
      </PrimaryButton>
      <OutlineButton onClick={handleReset}>Clear</OutlineButton>
    </Flex>
  );
};

export default DownloadReport;
