import usePostRequest from 'hooks/api/common/usePostRequest';
import endpoints from 'api/endpoints';
import { AxiosError } from 'axios';
import { useMutation } from 'react-query';

interface Payload {
  from: string;
  to: string;
}

export const useResetAndGenerateReport = (onSuccess: () => void) => {
  const resetRequest = usePostRequest(endpoints.assessmentReports);
  const generateRequest = usePostRequest(endpoints.assessmentReports);

  const resetAndGenerate = async ({
    path,
    data,
  }: {
    path: string;
    data: {
      from: string;
      to: string;
    };
  }) => {
    await resetRequest({}, path);
    await generateRequest(data);
  };

  return useMutation<
    any,
    AxiosError,
    {
      data: Payload;
      path: string;
    }
  >(({ data, path }) => resetAndGenerate({ data, path }), {
    onSuccess,
    throwOnError: true,
  });
};
