import React from 'react';
import styled from 'styled-components';
import { IconActionDocumentCsv } from '@cko/icons';
import { OutlineButton } from '@cko/primitives';

import useAlerts from 'hooks/useAlerts';
import useDownloadFile from 'hooks/useDownloadFile';

export const ExportIcon = styled(IconActionDocumentCsv)`
  margin-right: 5px;
  font-size: 16px;
  vertical-align: text-bottom;
`;

interface Props {
  url: string;
  fileName: string;
}

const ExportButton: React.FunctionComponent<Props> = ({ url, fileName }) => {
  const { download, loading } = useDownloadFile(url, fileName);
  const { showAlert } = useAlerts();

  return (
    <OutlineButton
      disabled={loading}
      onClick={async () => {
        try {
          await download();
        } catch (e) {
          showAlert(`Error exporting data`, {
            type: 'alert',
          });
        }
      }}
      data-cy="export-button"
    >
      <ExportIcon />
      {loading ? 'Exporting' : 'Export'}
    </OutlineButton>
  );
};

export default ExportButton;
