import {
  FunnelsResponse,
  Link,
  BaseLink,
  Pre3dsDecisions,
  PostAuthDecisions,
  PaymentProcessingDecisions,
} from 'types/models';
import { groupBy } from './groupBy';
import { calculateLinksRoundingPercentage } from './percentageRoundingCalculation';
import {
  decisionTypeMap,
  getDecisionDisplayValue,
  getPre3dsDecisionDisplayValueMap,
} from './decisions';
import { ThreeDSVersion } from 'contexts/ThreeDSVersionContext/ThreeDSVersionContext';

const transformedFunnel = (
  key: PaymentProcessingDecisions,
  version: ThreeDSVersion
) => {
  if (version === ThreeDSVersion.One) {
    return (
      getPre3dsDecisionDisplayValueMap(version)[key as Pre3dsDecisions] ?? key
    );
  }
  return key;
};

const createIncomingLinks = (link: BaseLink[], version: ThreeDSVersion) => {
  const linksGroupedBySource = groupBy(link, (link) => link.source);

  const incomingLinks = Object.keys(linksGroupedBySource).map((key) => {
    const group = linksGroupedBySource[key];

    const value = group.reduce((a, b) => a + b.value, 0);
    const target = transformedFunnel(
      key as PaymentProcessingDecisions,
      version
    );

    return {
      source: 'incoming',
      display_source: 'Incoming',
      target,
      display_target: getDecisionDisplayValue(
        target as PaymentProcessingDecisions,
        version
      ),
      // if the decision is decline we want to use transaction_count as the value
      // as this will be the correct value for incoming -> decline
      value:
        target === Pre3dsDecisions.Decline
          ? group.reduce((a, b) => a + b.transaction_count, 0)
          : value,
      id: `incoming_${key}`,
    };
  });

  return incomingLinks as BaseLink[];
};

const createLinks = (data: FunnelsResponse, version: ThreeDSVersion) => {
  const links = data.funnels
    .filter(
      (funnel) =>
        (funnel.transition.from.decision === Pre3dsDecisions.Decline &&
          funnel.transaction_count > 0) ||
        funnel.transition.to != null
    )
    .map((funnel) => {
      const to = funnel.transition.to || {
        step: 'post-auth',
        decision: PostAuthDecisions.Capture,
      };

      return {
        source: transformedFunnel(
          funnel.transition.from.decision as PaymentProcessingDecisions,
          version
        ),
        display_source: getDecisionDisplayValue(
          transformedFunnel(
            funnel.transition.from.decision as PaymentProcessingDecisions,
            version
          ) as PaymentProcessingDecisions,
          version
        ),
        target: transformedFunnel(
          to!.decision as PaymentProcessingDecisions,
          version
        ),
        display_target: getDecisionDisplayValue(
          transformedFunnel(
            to!.decision as PaymentProcessingDecisions,
            version
          ) as PaymentProcessingDecisions,
          version
        ),
        // if from decision is decline we want to insert an empty link between decline and capture
        // this way the sankey link appears to be cut off after decline
        value:
          funnel.transition.from.decision === Pre3dsDecisions.Decline
            ? 0
            : funnel.transaction_count,
        id: `${funnel.transition.from.decision}_${to!.decision}`,
        transaction_count: funnel.transaction_count,
      };
    });

  const linksGrouped = groupBy(links, (link) => link.source + link.target);

  const incomingLinks = Object.keys(linksGrouped).map((key) => {
    const group = linksGrouped[key];
    return {
      ...group[0],
      value: group.reduce((a, b) => a + b.value, 0),
    };
  });

  return incomingLinks as BaseLink[];
};

type LinkWithType = BaseLink & {
  type: string;
};

const calculatePercentage = (links: BaseLink[]) => {
  const linksWithType = links.map((link) => ({
    ...link,
    type:
      decisionTypeMap[link.target as PaymentProcessingDecisions] || 'incoming',
  })) as LinkWithType[];

  const linksByType = groupBy(linksWithType, (link: LinkWithType) => {
    return link.type;
  });

  const linkTotalByType = Object.keys(linksByType).reduce((acc, key) => {
    const links = linksByType[key];

    acc[key] = links.reduce((sum, link) => sum + link.value, 0);

    return acc;
  }, {} as Record<string, number>);

  const linksWithPercentage = linksWithType.map((link) => {
    const groupTotal = linkTotalByType[link.type];
    const percentage = (link.value / groupTotal) * 100;

    return { ...link, percentage } as Link;
  });

  return calculateLinksRoundingPercentage(linksWithPercentage);
};

export const transformData = (
  data: FunnelsResponse,
  version: ThreeDSVersion
) => {
  const links = createLinks(data, version);

  const incomingLinks = createIncomingLinks(links, version);
  const linksByPercentage = calculatePercentage([...incomingLinks, ...links]);
  return linksByPercentage;
};

export const hasValidTransitions = (data: FunnelsResponse): boolean => {
  for (let i = 0; i < data.funnels.length; i++) {
    const { transition } = data.funnels[i];

    if (transition.from && transition.to) {
      return true;
    }
  }
  return false;
};
