import { useEffect, useState } from 'react';
import useGetRequest from 'hooks/api/common/useGetRequest';
import { useQuery } from 'react-query';
import { AssessmentReportsResponse } from 'types/models';
import endpoints from 'api/endpoints';

export const useAssessmentReports = () => {
  const req = useGetRequest();
  const [isPending, setIsPending] = useState<boolean | null>(null);

  const queryOptions = useQuery<AssessmentReportsResponse>(
    endpoints.assessmentReports,
    () => req<AssessmentReportsResponse>(endpoints.assessmentReports),
    {
      refetchInterval: 1000,
      enabled: isPending === null || isPending,
    }
  );

  useEffect(() => {
    if (queryOptions.data) {
      setIsPending(queryOptions.data.status === 'Pending');
    } else if (queryOptions.isError) {
      setIsPending(false);
    }
  }, [queryOptions]);

  return queryOptions;
};
