import format from 'date-fns/format';
import { DateRange } from '@cko/calendar';

const DEFAULT_FORMAT = 'dd MMM yyyy - HH:mm';
const API_FORMAT = 'yyyy-MM-dd';

const getUTCDate = (dateString: string) => {
  const date = new Date(dateString);

  return new Date(
    date.getUTCFullYear(),
    date.getUTCMonth(),
    date.getUTCDate(),
    date.getUTCHours(),
    date.getUTCMinutes(),
    date.getUTCSeconds()
  );
};

export const stringToDateDisplayValue = (
  date: string,
  dateFormater: string = DEFAULT_FORMAT
) => format(getUTCDate(date), dateFormater);

export const formatDate = (date: Date, dateFormater: string = API_FORMAT) => {
  return format(date, dateFormater);
};

export const formatDateRange = (
  dateRange: DateRange,
  dateFormater: string = API_FORMAT
) => {
  let fromDate = null;
  let toDate = null;
  if (dateRange.from) {
    fromDate = formatDate(dateRange.from, dateFormater);
  }
  if (dateRange.to) {
    toDate = formatDate(dateRange.to, dateFormater);
  }
  return { fromDate, toDate };
};
