import React from 'react';
import InfoTooltip from 'components/InfoTooltip';
import { Text, Flex, Box, AlertButton } from '@cko/primitives';
import { MutateFunction } from 'react-query';
import { AxiosError } from 'axios';

interface Props {
  onCancel: MutateFunction<
    {},
    AxiosError<{}>,
    {
      data: {};
      path?: string | undefined;
    },
    unknown
  >;
  reportId: string;
}

const PendingReport: React.FunctionComponent<Props> = ({
  onCancel,
  reportId,
}) => {
  const handleCancel = () => {
    onCancel({ data: {}, path: `${reportId}/cancel` });
  };

  return (
    <Flex alignItems="center">
      <Box mr={2}>
        <InfoTooltip text="This can take a few minutes, you may navigate away from this page and your report will be ready when you return." />
      </Box>
      <Box mr={2}>
        <Text>Report being generated</Text>
      </Box>
      <AlertButton onClick={handleCancel}>Cancel</AlertButton>
    </Flex>
  );
};

export default PendingReport;
