import { useAddUpdateMutation } from 'hooks/api/common/useAddUpdateMutation';
import endpoints from 'api/endpoints';
import { AxiosError } from 'axios';
import { MutateConfig } from 'react-query';

interface Payload {
  from: string;
  to: string;
}

export const useGenerateAssessmentReport = (
  opts: MutateConfig<{}, AxiosError<{}>, { data: Payload; path?: string }>
) => {
  return useAddUpdateMutation<Payload>(
    'post',
    endpoints.assessmentReports,
    opts
  );
};
