import React from 'react';
import { Flex, Box, InlineNotification } from '@cko/primitives';
import GenerateAssesmentReport from './GenerateAssesmentReport';
import { formatDate } from 'utils/dates/dateFormatter';
import { AxiosError } from 'axios';
import { MutateFunction } from 'react-query';

interface Props {
  reportId: string;
  resetAndGenerate: MutateFunction<
    any,
    AxiosError<any>,
    {
      data: {
        from: string;
        to: string;
      };
      path: string;
    },
    unknown
  >;
  from: string;
  to: string;
}

const AbortedReport: React.FunctionComponent<Props> = ({
  reportId,
  resetAndGenerate,
  from,
  to,
}) => {
  const handleResetAndGenerate = () => {
    resetAndGenerate({
      data: { from: formatDate(new Date(from)), to: formatDate(new Date(to)) },
      path: `${reportId}/reset`,
    });
  };

  return (
    <Flex alignItems="center">
      <Box mr={2}>
        <InlineNotification type="alert">
          Sorry, there was an error generating your report. Please try again.
        </InlineNotification>
      </Box>
      <GenerateAssesmentReport generateReport={handleResetAndGenerate} />
    </Flex>
  );
};

export default AbortedReport;
