import React, { useEffect } from 'react';
import { Box } from '@cko/primitives';
import GenerateAssesmentReport from './GenerateAssesmentReport';
import PendingReport from './PendingReport';
import DownloadReport from './DownloadReport';
import AbortedReport from './AbortedReport';
import {
  useGenerateAssessmentReport,
  useAssessmentReports,
  useCancelAssessmentReport,
  useResetAssessmentReport,
  useResetAndGenerateReport,
} from 'hooks/api/assesment-reports';
import { formatDate } from 'utils/dates/dateFormatter';
import useMerchants from 'hooks/useMerchants';

interface Props {
  from: Date;
  to: Date;
}

const DownloadTransactionReportContainer: React.FC<Props> = ({ from, to }) => {
  const [{ selectedMerchant }] = useMerchants();

  const { data, refetch } = useAssessmentReports();

  const queryOpts = {
    onSuccess: () => {
      refetch();
    },
  };

  useEffect(() => {
    refetch();
  }, [selectedMerchant, refetch]);

  const [generateAssementReport] = useGenerateAssessmentReport(queryOpts);
  const [cancelReport] = useCancelAssessmentReport(queryOpts);
  const [resetReport] = useResetAssessmentReport(queryOpts);
  const [resetAndGenerate] = useResetAndGenerateReport(queryOpts.onSuccess);

  if (!data) {
    return null;
  }

  const generateReport = () => {
    generateAssementReport({
      data: { from: formatDate(from), to: formatDate(to) },
    });
  };

  return (
    <Box>
      {data.status === 'Initial' && (
        <GenerateAssesmentReport generateReport={generateReport} />
      )}
      {data.status === 'Pending' && (
        <PendingReport onCancel={cancelReport} reportId={data.id} />
      )}
      {data.status === 'Completed' && (
        <DownloadReport
          from={data.from}
          to={data.to}
          reportId={data.id}
          onClear={resetReport}
        />
      )}
      {data.status === 'Aborted' && (
        <AbortedReport
          reportId={data.id}
          from={data.from}
          to={data.to}
          resetAndGenerate={resetAndGenerate}
        />
      )}
    </Box>
  );
};

export default DownloadTransactionReportContainer;
