import React, { useState } from 'react';
import { Tooltip } from '@cko/tooltip';
import { Placement } from '@popperjs/core';
import { Box } from '@cko/primitives';
import 'styled-components/macro';

interface Props {
  text: string | undefined;
  enabled: boolean;
  defaultDirection?: Placement;
}

const TooltipButtonWrapper: React.FunctionComponent<Props> = ({
  text,
  children,
  enabled,
  defaultDirection = 'top',
}) => {
  const [show, setShow] = useState(false);

  if (!enabled || !text) return <>{children}</>;

  return (
    <Tooltip text={text} show={show} defaultDirection={defaultDirection}>
      <Box
        css={`
          button {
            pointer-events: ${enabled ? 'none' : 'auto'};
          }
        `}
        onMouseEnter={() => setShow(true)}
        onMouseLeave={() => setShow(false)}
      >
        {children}
      </Box>
    </Tooltip>
  );
};

export default TooltipButtonWrapper;
