import React, { useMemo, useState } from 'react';
import {
  Panel,
  Box,
  Flex,
  TextHeadingTwo,
  SimpleSelectMenu,
  FormLabel,
  InlineNotification,
} from '@cko/primitives';
import { DateRangeFilter, useDateRange } from '@cko/calendar';
import Sankey from 'components/Sankey/Sankey';
import {
  MasterDetailsHeaderLayout,
  MasterDetailsHeaderLayoutRight,
} from 'components/MasterDetailsHeader';
import useMerchants from 'hooks/useMerchants';
import { subMonths, isBefore, isAfter, startOfTomorrow } from 'date-fns';
import { hasValidTransitions, transformData } from 'utils/sankey';
import { useTransactionSummariesData } from 'hooks/api/dashboard';
import ThreeDSVersionToggle from 'components/ThreeDSVersionToggle';
import { useThreeDSVersion } from 'contexts/ThreeDSVersionContext';
import DownloadTransactionReportContainer from './../DownloadTransactionReportContainer';
import { useFeatureFlags } from 'contexts/FeatureFlagsContext';

export enum SankeyDisplayValue {
  count = 'count',
  percentage = 'percentage',
}

const sankeyDisplayOptions = [
  {
    value: SankeyDisplayValue.count,
    label: 'Number of transactions',
  },
  { value: SankeyDisplayValue.percentage, label: 'Percentage of transactions' },
];

const SankeyContainer: React.FunctionComponent = () => {
  const { featureFlags } = useFeatureFlags();

  const { from, to, onDatesChanged } = useDateRange(
    subMonths(new Date(), 3),
    new Date()
  );

  const { status, resolvedData } = useTransactionSummariesData({
    from: from,
    to: to,
  });

  const [{ selectedMerchant }] = useMerchants();

  const [
    sankeyDisplayValue,
    setSankeyDisplayValue,
  ] = useState<SankeyDisplayValue>(SankeyDisplayValue.count);

  const { version } = useThreeDSVersion();

  const dataset = useMemo(() => {
    if (!resolvedData || !hasValidTransitions(resolvedData)) {
      return null;
    }

    return transformData(resolvedData, version);
  }, [version, resolvedData]);

  const renderSankeyContent = () => {
    if (status === 'loading') {
      return (
        <Flex height="400px" alignItems="center" justifyContent="center">
          <InlineNotification type="success">Loading...</InlineNotification>
        </Flex>
      );
    }

    if (status === 'error') {
      return (
        <Flex height="400px" alignItems="center" justifyContent="center">
          <InlineNotification type="alert">
            Something went wrong, please try again later.
          </InlineNotification>
        </Flex>
      );
    }

    if (dataset === null) {
      return (
        <Flex height="400px" alignItems="center" justifyContent="center">
          <InlineNotification type="warning">
            You currently do not have enough data to generate a graph.
          </InlineNotification>
        </Flex>
      );
    }

    return <Sankey data={dataset} sankeyDisplayValue={sankeyDisplayValue} />;
  };

  const handleChangeSankeyValueType = (
    item: typeof sankeyDisplayOptions[0]
  ) => {
    setSankeyDisplayValue(item.value);
  };

  const isCkoMerchant = selectedMerchant?.id === null;

  return (
    <>
      <Panel p={0} mb={3}>
        <Box boxShadow="focusLight.0">
          <MasterDetailsHeaderLayout>
            <Flex alignItems="center">
              <TextHeadingTwo>
                Live performance and transaction routing
              </TextHeadingTwo>
            </Flex>
            <MasterDetailsHeaderLayoutRight>
              {!isCkoMerchant &&
                from &&
                featureFlags.DOWNLOAD_TRANSACTION_SUMMARIES && (
                  <DownloadTransactionReportContainer
                    from={from}
                    to={to || from}
                  />
                )}
            </MasterDetailsHeaderLayoutRight>
          </MasterDetailsHeaderLayout>
        </Box>

        <Flex p={3} alignItems="center" justifyContent="space-between">
          <Flex>
            <FormLabel mr={2}>View by: </FormLabel>

            <SimpleSelectMenu
              data-testId="select-value-type-menu"
              initialSelectedItem={sankeyDisplayOptions[0]}
              onChange={handleChangeSankeyValueType}
              options={sankeyDisplayOptions}
              isFilterable={false}
            />
          </Flex>

          <Flex>
            <Box mr={3}>
              <ThreeDSVersionToggle />
            </Box>
            <Box p="0" width="285px">
              <DateRangeFilter
                from={from}
                to={to}
                onChange={onDatesChanged}
                isOutsideValidRange={(date: any) => {
                  return (
                    isAfter(date, startOfTomorrow()) ||
                    isBefore(date, subMonths(new Date(), 3))
                  );
                }}
              />
            </Box>
          </Flex>
        </Flex>
        {renderSankeyContent()}
      </Panel>
    </>
  );
};

export default SankeyContainer;
