import { ThreeDSVersion } from 'contexts/ThreeDSVersionContext';
import {
  Pre3dsDecisions,
  PostAuthDecisions,
  PaymentProcessingStep,
  PaymentProcessingDecisions,
} from '../types/models/index';

const convertDecisionsToActions = (decisionMap: Record<string, string>) => {
  return Object.entries(decisionMap).map(([value, label]) => {
    return { value: { type: 'decision', value }, label };
  });
};

export const pre3dsDecisionDescriptionMap: Record<Pre3dsDecisions, string> = {
  [Pre3dsDecisions.Decline]: `Your transaction won't be processed. It will be sent neither to 3DS nor to Authorisation.`,
  [Pre3dsDecisions.Challenge]: `Your transaction will be routed via the 3DS workflow which means that will you be covered by the liability shift (on fraudulent grounds only).`,
  [Pre3dsDecisions.NoPreference]: `Your transaction will be routed via the 3DS workflow which means that will you be covered by the liability shift (on fraudulent grounds only).`,
  [Pre3dsDecisions.FrictionlessLiability]: `You will be requesting the Issuer to authenticate your transaction without using any challenge, which they may or may not accept.`,
  [Pre3dsDecisions.TryExemptions]:
    'Whenever possible, our intelligent 3DS routing engine will select the best PSD2 exemptions to downgrade your transaction.',
};

const pre3ds2DecisionDisplayValueMap = {
  [Pre3dsDecisions.Decline]: 'Decline',
  [Pre3dsDecisions.Challenge]: 'Force Challenge (liability shift)',
  [Pre3dsDecisions.NoPreference]: 'No Preference (liability shift)',
  [Pre3dsDecisions.FrictionlessLiability]: 'Try Frictionless (liability shift)',
  [Pre3dsDecisions.TryExemptions]: 'Try Exemptions',
} as const;

const pre3ds1DecisionDisplayValueMap = {
  [Pre3dsDecisions.Decline]: 'Decline',
  [Pre3dsDecisions.Challenge]: '3ds check',
  [Pre3dsDecisions.NoPreference]: '3ds check',
  [Pre3dsDecisions.FrictionlessLiability]: '3ds check',
  [Pre3dsDecisions.TryExemptions]: 'Accept',
} as const;

export const getPre3dsDecisionDisplayValueMap = (version: ThreeDSVersion) => {
  return {
    [ThreeDSVersion.One]: pre3ds1DecisionDisplayValueMap,
    [ThreeDSVersion.Two]: pre3ds2DecisionDisplayValueMap,
  }[version];
};

export const postAuthDecisionDisplayValueMap: Record<
  PostAuthDecisions,
  string
> = {
  [PostAuthDecisions.Capture]: 'Capture',
  [PostAuthDecisions.Flag]: 'Flag',
  [PostAuthDecisions.Void]: 'Void',
};

export const threeDS1DecisionFilter = (version: ThreeDSVersion | undefined) => (
  decision: Pre3dsDecisions
) => {
  return version === '3DS1'
    ? ![
        Pre3dsDecisions.Challenge,
        Pre3dsDecisions.FrictionlessLiability,
      ].includes(decision)
    : true;
};

export type DecisionList = {
  value: string;
  label: string;
}[];

export const getDecisionDisplayValue = (
  key: PaymentProcessingDecisions,
  version: ThreeDSVersion
) => {
  return (
    getPre3dsDecisionDisplayValueMap(version)[key as Pre3dsDecisions] ||
    postAuthDecisionDisplayValueMap[key as PostAuthDecisions] ||
    key
  );
};

export const getPre3dsDecisionActions = (version: ThreeDSVersion) =>
  convertDecisionsToActions(getPre3dsDecisionDisplayValueMap(version));

export const postAuthDecisionDescriptionMap: Record<
  PostAuthDecisions,
  string
> = {
  [PostAuthDecisions.Capture]: '',
  [PostAuthDecisions.Flag]:
    'Coming soon. Transaction will put on hold for manually review. You will be able to access flagged transactions and take decisions in the Payments tab. ',
  [PostAuthDecisions.Void]: '',
};

export const postAuthDecisionActions = convertDecisionsToActions(
  postAuthDecisionDisplayValueMap
);

type PaymentProcessingDecisionsMap = Record<PaymentProcessingDecisions, string>;

export const getDecisionMap = (
  step: PaymentProcessingStep,
  version: ThreeDSVersion
) => {
  switch (step) {
    case PaymentProcessingStep.Pre3ds: {
      if (version === ThreeDSVersion.One) {
        return {
          displayValues: pre3ds1DecisionDisplayValueMap as PaymentProcessingDecisionsMap,
          descriptions: pre3dsDecisionDescriptionMap as PaymentProcessingDecisionsMap,
        };
      }
      return {
        displayValues: pre3ds2DecisionDisplayValueMap as PaymentProcessingDecisionsMap,
        descriptions: pre3dsDecisionDescriptionMap as PaymentProcessingDecisionsMap,
      };
    }
    case PaymentProcessingStep.PostAuth:
      return {
        displayValues: postAuthDecisionDisplayValueMap as PaymentProcessingDecisionsMap,
        descriptions: postAuthDecisionDescriptionMap as PaymentProcessingDecisionsMap,
      };
  }
};

export const decisionPriorityMap = {
  [Pre3dsDecisions.Decline]: 1,
  [Pre3dsDecisions.Challenge]: 2,
  [Pre3dsDecisions.NoPreference]: 3,
  [Pre3dsDecisions.FrictionlessLiability]: 4,
  [Pre3dsDecisions.TryExemptions]: 5,
  [PostAuthDecisions.Void]: 1,
  [PostAuthDecisions.Flag]: 2,
  [PostAuthDecisions.Capture]: 3,
};

export const isPre3dsDecision = (decision: Pre3dsDecisions) => {
  return pre3dsDecisionDescriptionMap[decision] !== undefined;
};

export const decisionTypeMap: Record<
  Pre3dsDecisions | PostAuthDecisions,
  string
> = {
  [Pre3dsDecisions.Decline]: PaymentProcessingStep.Pre3ds,
  [Pre3dsDecisions.Challenge]: PaymentProcessingStep.Pre3ds,
  [Pre3dsDecisions.NoPreference]: PaymentProcessingStep.Pre3ds,
  [Pre3dsDecisions.FrictionlessLiability]: PaymentProcessingStep.Pre3ds,
  [Pre3dsDecisions.TryExemptions]: PaymentProcessingStep.Pre3ds,
  [PostAuthDecisions.Void]: PaymentProcessingStep.PostAuth,
  [PostAuthDecisions.Flag]: PaymentProcessingStep.PostAuth,
  [PostAuthDecisions.Capture]: PaymentProcessingStep.PostAuth,
};
