import React from 'react';
import { Box, SegmentedControl } from '@cko/primitives';
import {
  ThreeDSVersion,
  useThreeDSVersion,
} from 'contexts/ThreeDSVersionContext';

const ThreeDSVersionToggle: React.FC = () => {
  const { version, setVersion } = useThreeDSVersion();

  return (
    <Box bg="white" width="300px">
      <SegmentedControl
        options={[
          { label: '3DS 1 view', value: '3DS1' },
          { label: '3DS 2 view', value: '3DS2' },
        ]}
        onChange={(event) => setVersion(event.target.value as ThreeDSVersion)}
        selectedValue={version}
      />
    </Box>
  );
};

export default ThreeDSVersionToggle;
