import { usePaginatedQuery } from 'react-query';
import endpoints from 'api/endpoints';
import useGetRequest from 'hooks/api/common/useGetRequest';
import { FormattedDateRange, FunnelsResponse } from 'types/models';
import { formatDateRange } from 'utils/dates/dateFormatter';
import { DateRange } from '@cko/calendar';

export const useTransactionSummariesData = (dateRange: DateRange) => {
  const req = useGetRequest();
  const formattedDateRange = formatDateRange(dateRange);
  return usePaginatedQuery<FunnelsResponse, [string, FormattedDateRange]>(
    [endpoints.transactionSummaries, formattedDateRange],

    (_, { toDate, fromDate }) => {
      const queryString =
        fromDate && !toDate
          ? `from=${fromDate}&to=${fromDate}`
          : `from=${fromDate}&to=${toDate}`;
      return req(`${endpoints.transactionSummaries}?${queryString}`);
    },
    {
      enabled: !!formattedDateRange.fromDate,
    }
  );
};
