import { useAddUpdateMutation } from 'hooks/api/common/useAddUpdateMutation';
import endpoints from 'api/endpoints';
import { AxiosError } from 'axios';
import { MutateConfig } from 'react-query';

export const useCancelAssessmentReport = (
  opts: MutateConfig<{}, AxiosError<{}>, { data: {}; path?: string }>
) => {
  return useAddUpdateMutation('post', endpoints.assessmentReports, opts);
};
